.input-container {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
}

.input-login {
    padding: 0.5em;
    border-radius: 4px;
    border: 2px solid black;
    outline: none;
    margin-bottom: 10px;
    transition: all 0.2s linear;
}

.input-login:focus {
    box-shadow: 0px 0px 3px 3px var(--gray);
}



.input-label {
    margin-left: 2px;
    margin-bottom: 5px;
    font-weight: 400;
}

.error {
    height: 34px;
    border-radius: 4px;
    outline: none;
    padding: 5px;
    margin-bottom: 10px;
    border: 2px solid var(--error);
}