@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html {
    font-size: 62.5%;
}

body{
    font-size: 1.6rem;
}

:root {
    --main: #154B90;
    --middle-light-blue: #436ea6;
    --light-blue: #7293bc;
    --middle-dark-blue: #103c73;
    --dark-blue: #0c2d56;
    --cyan: #158990;
    --green: #4b9015;
    --dark-pink: #90154b;
    --brown: #905a15;
    --red-brown: #902515;
    --error: red;
    --desert-sand-brown: #908215;
    --purple-blue: #381590;
    --purple: #751590;
    --white: #EEEEEE;
    --gray: #c4c4c4;
    --dark-gray: #333333;
}

.error {
    color: red;
}

input[type="text"] {
    font-size: 1em;
}