.button-container {
    margin-top: 10px;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    color: white;
    background-color: var(--main);
    border-radius: 4px;
    width: 100%;
    border: 0;
    transition: all 0.3s ease;
}

.button-container:hover {
    cursor: pointer;
    background-color: var(--middle-dark-blue);
}

.button-container:active {
    background-color: white;
    box-shadow: 0px 0px 2px 2px var(--middle-dark-blue);
    color: var(--main);
}