.main-container {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* margin-block: 5em; */
}

.user-login-container {
    display: flex;
    position: relative;
    flex-direction: column;
    /* margin-block: 1em; */
    padding: 1em;
    width: 600px;
}

@media (max-width: 768px) {
    .user-login-container {
        width: 100%;
    }

    .user-accessibility-message {
        font-size: 24px;
    }
}