.label {
    margin-left: 2px;
    margin-top: 10px;
    margin-bottom: 5px;
    font-weight: 600;

}

.select {
    outline: none;
    height: 34px;
    border: 2px solid black;
    border-radius: 4px;
    margin-bottom: 5px;
}

.select-with-error {
    outline: none;
    height: 34px;
    border: 2px solid var(--error);
    border-radius: 4px;
    margin-bottom: 5px;

}