.user-accessibility-message {
    margin-bottom: 1em;
}

.container-custom {
    margin-top: 2em;
}

.form-login {
    display: flex;
    flex-direction: column;
}

.input-container {
    display: flex;
    flex-direction: column;
}

.input-login {
    height: 34px;
    border-radius: 4px;
    border: 2px solid var(--gray);
    outline: none;
    padding: 5px;
    margin-bottom: 10px;
}

.input-login:focus {
    border-color: var(--dark-gray);
}

.input-label {
    margin-left: 2px;
    margin-bottom: 5px;
    font-weight: 600;
}

.error {
    color: var(--error);
    margin-bottom: 10px;
}