.select-card-container {
    padding: 10px;
    margin-bottom: 10px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.select-card-name {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding-block: 1em;
}

.select-card-container:last-child {
    margin-bottom: 5px;
}

.select-card-container:hover {
    cursor: pointer;
}

.icon-chevron {
    font-size: 24px;
    transform: rotate(0deg);
    transition: all 500ms ease;
}

.icon-chevron-down {
    font-size: 24px;
    transform: rotate(90deg);
    transition: all 500ms ease;
}

.select-options {
    display: flex;
    flex-direction: column;
    height: 0px;
    overflow: hidden;
}

.option {
    padding: 10px;
    transition: all 0.2s ease-in-out;
}

.open {
    animation: optionReveal 500ms linear 100ms forwards;
}

.option:hover {
    background-color: var(--middle-light-blue);
    color: white;
}

@keyframes optionReveal {
    0% {
        opacity: 0;
    }

    100% {
        height: 120px;
        opacity: 1;
    }
}

@keyframes optionClose {
    0% {
        height: 120px;
        opacity: 1;
    }
    100% {
        opacity: 0;
        height: 0px;
    }
}